<template>
	<view class="page-container">
		<!-- 顶部背景 -->
		<view class="bg-header"></view>
		<view class="enterImg">
			<image src="/static/image/enter.png" mode=""></image>

			<view class="info-itme">
				<view class="grid-list">
					<view class="grid-item" v-for="(item, index) in listData" :key="index" @click="goToDetail(item)">
						<image :src="httpUrl+item.picUrl" mode="aspectFill"></image>
						<view class="item-title">{{item.name}}</view>
					</view>
				</view>
			</view>
			<!-- 添加占位区域，防止内容被底部footer遮挡 -->
			<view class="footer-placeholder"></view>
		</view>
		
	</view>
</template>

<script>
	import { userApi } from '../../../api/user.js';
	import settings from '../../../common/settings.js';
	export default {
		data() {
			return {
				httpUrl:'',
				listData: [{
						id: 1,
						image: '/static/imgCs/1.png',
						title: '燕麦干草'
					},
					{
						id: 2,
						image: '/static/imgCs/1.png',
						title: '燕麦柔丝'
					},
					{
						id: 3,
						image: '/static/imgCs/1.png',
						title: '苜蓿干草'
					},
					{
						id: 4,
						image: '/static/imgCs/1.png',
						title: '裹包苜蓿青储'
					},
					{
						id: 5,
						image: '/static/imgCs/1.png',
						title: '小麦秸秆'
					},
					{
						id: 6,
						image: '/static/imgCs/1.png',
						title: '小麦秸秆柔丝'
					},
					{
						id: 7,
						image: '/static/imgCs/1.png',
						title: '治沙稻草'
					},
					{
						id: 8,
						image: '/static/imgCs/1.png',
						title: '铡段稻草'
					}, {
						id: 9,
						image: '/static/imgCs/1.png',
						title: '柔丝稻草'
					},
					{
						id: 10,
						image: '/static/imgCs/1.png',
						title: '人工羊草'
					},
					{
						id: 11,
						image: '/static/imgCs/1.png',
						title: '玉米青储'
					}
				]
			};
		},
		onLoad(options) {
			this.httpUrl = settings.devUrl
			console.log(options)
			this.getList()
		},
		onShow() {
			// 设置导航栏背景色与bg-header起始颜色一致
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: '#63B872'
			})
		},
		methods: {
			// 接口请求
			getList() {
				// 品类列表
				userApi.getMucaoList().then(res => {
					console.log(res.data)
					this.listData = res.data
				})
			},
			goToDetail(item) {
				// 点击跳转到详情页
				uni.navigateTo({
					// url: '/pages/tabbar/index/grassDetails/grassDetails?id=' + item.id
					url: '/subpkg_index/pages/enterGrass/enterGrass?id=' + item.id + '&name=' + item.name
				})
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f5f6f9;
	}

	.footer-placeholder {
		height: 60rpx;
		/* 底部footer的高度+padding */
	}

	.info-itme {
		background-color: #fff;
		border-radius: 24rpx;
		padding: 30rpx;
		margin-bottom: 20rpx;
	}

	.grid-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.grid-item {
		width: 48%;
		margin-bottom: 20rpx;

		image {
			width: 100%;
			height: 200rpx;
			border-radius: 12rpx;
		}

		.item-title {
			font-size: 28rpx;
			color: #333;
			text-align: center;
			padding: 10rpx 0;
		}
	}

	.enterImg {
		width: 100%;
		height: 190rpx;

		image {
			width: 100%;
			height: 190rpx;
		}
	}

	.bg-header {
		left: 0rpx;
		top: -34rpx;
		width: 100%;
		height: 724rpx;
		opacity: 1;
		border-radius: 0rpx 0rpx, 16rpx, 16rpx;
		background: linear-gradient(180deg, rgba(99, 184, 114, 1) 0%, rgba(245, 246, 249, 1) 100%);
		position: absolute;
		z-index: -1;
	}

	.page-container {
		// position: relative;
		padding: 0 30rpx;
	}
</style>